* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}
ul {
  list-style: none;
}
.country-item{
  text-decoration: none;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}
.country__name{
 
  font-size: 20px;
  font-weight: 700;
 
  margin-left: 20px;

}
.menu__country-item-inform{
  text-decoration: none;
  word-spacing: 5px;
}
.input-country{
  margin-left: 30px;
  border-color: blue;
}